import React from "react";
import { ResponsiveLine } from "@nivo/line";
import "./visuals.css";

const Visuals = (props) => {
  let data = [
    {
      id: "confirmed",
      color: "orange",
    },
    {
      id: "recovered",
      color: "hsl(120, 100%, 25%)",
    },
    {
      id: "deaths",
      color: "red",
    },
  ];

  data[0].data = props.data.map((item) => {
    return { y: parseInt(item.dailyconfirmed), x: item.date };
  });
  data[1].data = props.data.map((item) => {
    return { y: parseInt(item.dailyrecovered), x: item.date };
  });
  data[2].data = props.data.map((item) => {
    return { y: parseInt(item.dailydeceased), x: item.date };
  });
  return (
    <div className="charts">
      <ResponsiveLine
        data={data}
        margin={{ top: 50, right: 110, bottom: 70, left: 60 }}
        xScale={{ type: "point" }}
        yScale={{
          type: "linear",
          min: "auto",
          max: "auto",
          stacked: false,
          reverse: false,
        }}
        axisBottom={{
          orient: "bottom",
          tickSize: 5,
          tickPadding: 12,
          tickRotation: 90,
          legend: "date",
          legendOffset: 36,
          legendPosition: "middle",
        }}
        axisTop={null}
        axisRight={null}
        axisLeft={{
          orient: "left",
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
          legend: "count",
          legendOffset: -40,
          legendPosition: "middle",
        }}
        colors={{ scheme: "nivo" }}
        pointSize={10}
        pointColor={{ theme: "background" }}
        pointBorderWidth={2}
        pointBorderColor={{ from: "serieColor" }}
        pointLabel="y"
        pointLabelYOffset={-12}
        useMesh={true}
        legends={[
          {
            anchor: "bottom-right",
            direction: "column",
            justify: false,
            translateX: 100,
            translateY: 0,
            itemsSpacing: 0,
            itemDirection: "left-to-right",
            itemWidth: 80,
            itemHeight: 20,
            itemOpacity: 0.75,
            symbolSize: 12,
            symbolShape: "circle",
          },
        ]}
      />
    </div>
  );
};

export default Visuals;
