import React from "react";
import "./statewise.css";

const Statewise = (props) => {
  let rows = props.data.splice(1, props.data.length - 1).map((item) => {
    return (
      <tr key={item.state}>
        <td>{item.state}</td>
        <td>
          <p>Active</p>
          {item.active}{" "}
          <sub>
            {item.deltaconfirmed}
            <i
              className={
                item.deltaconfirmed > 0 ? "fa fa-arrow-up" : "fa fa-arrow-down"
              }
            ></i>
          </sub>
        </td>
        <td>
          <p>Recovered</p>
          {item.recovered}{" "}
          <sub>
            {item.deltarecovered}
            <i
              className={
                item.deltarecovered > 0 ? "fa fa-arrow-up" : "fa fa-arrow-down"
              }
            ></i>
          </sub>
        </td>
        <td>
          <p>Deaths</p>
          {item.deaths}{" "}
          <sub>
            {item.deltadeaths}
            <i
              className={
                item.deltadeaths > 0 ? "fa fa-arrow-up" : "fa fa-arrow-down"
              }
            ></i>
          </sub>
        </td>
      </tr>
    );
  });
  return (
    <table>
      <tbody>{rows}</tbody>
    </table>
  );
};

export default Statewise;
